ssible\"\n\t\t\t\t@click=\"sign\">\n\t\t</div>\n\t</AppSidebarTab>\n</template>\n\n<script>\nimport AppSidebarTab from '@nextcloud/vue/dist/Components/AppSidebarTab'\nimport axios from '@nextcloud/axios'\nimport { generateUrl } from '@nextcloud/router'\n\nimport { getFilePickerBuilder } from '@nextcloud/dialogs'\nimport { translate as t } from '@nextcloud/l10n'\n\nexport default {\n\tname: 'SignerTab',\n\n\tcomponents: {\n\t\tAppSidebarTab,\n\t},\n\tmixins: [],\n\n\tprops: {\n\t\tfileInfo: {\n\t\t\ttype: Object,\n\t\t\tdefault: () => {},\n\t\t\trequired: true,\n\t\t},\n\t},\n\tdata() {\n\t\treturn {\n\t\t\tsignaturePath: '',\n\t\t\tpassword: '',\n\t\t\tresponse: '',\n\t\t\ticon: 'icon-rename',\n\t\t\tupdating: false,\n\t\t\tloading: true,\n\t\t\tname: t('signer', 'Assinar Documento'),\n\t\t}\n\t},\n\n\tcomputed: {\n\t\tid() {\n\t\t\treturn 'signerTab'\n\t\t},\n\t\tactiveTab() {\n\t\t\treturn this.$parent.activeTab\n\t\t},\n\t\tsavePossible() {\n\t\t\treturn (\n\t\t\t\tthis.password !== ''\n\t\t\t\t&& this.signaturePath !== ''\n\t\t\t)\n\t\t},\n\t},\n\tmethods: {\n\t\tasync sign() {\n\t\t\tthis.updating = true\n\t\t\tthis.response = ''\n\t\t\tthis.error = ''\n\t\t\ttry {\n\t\t\t\tconst response = await axios.post(\n\t\t\t\t\tgenerateUrl('/apps/signer/api/0.1/sign'),\n\t\t\t\t\t{\n\t\t\t\t\t\tinputFilePath: OC.joinPaths(this.fileInfo.get('path'), this.fileInfo.get('name')),\n\t\t\t\t\t\toutputFolderPath: this.fileInfo.get('path'),\n\t\t\t\t\t\tcertificatePath: this.signaturePath,\n\t\t\t\t\t\tpassword: this.password,\n\t\t\t\t\t}\n\t\t\t\t)\n\t\t\t\tif (!response.data || !response.data.fileSigned) {\n\t\t\t\t\tthrow new Error(response.data)\n\t\t\t\t}\n\t\t\t\tthis.response = t('signer', 'Documento assinado disponivel em ') + response.data.fileSigned\n\n\t\t\t} catch (e) {\n\t\t\t\tconsole.error(e)\n\t\t\t\tthis.error = t('signer', 'No foi possivel assinar documento!')\n\t\t\t}\n\t\t\tthis.updating = false\n\t\t},\n\n\t\tpickFromCloud() {\n\t\t\tconst picker = getFilePickerBuilder(t('signer', 'Escolha o local da assinatura'))\n\t\t\t\t.setMultiSelect(false)\n\t\t\t\t.addMimeTypeFilter('application/octet-stream')\n\t\t\t\t.setModal(true)\n\t\t\t\t.setType(1)\n\t\t\t\t.allowDirectories(false)\n\t\t\t\t.build()\n\n\t\t\tpicker.pick().then((path) => {\n\t\t\t\tthis.signaturePath = path\n\t\t\t})\n\t\t},\n\t},\n}\n</script>\n\n<style>\n\n#signerTabContent {\n\tdisplay: flex;\n\tflex-direction: column;\n}\n\n.form-group > input {\n\twidth: 50%;\n}\n\n.form-group > input[type='button'] {\n\twidth: 80%;\n\tmargin: 2em;\n}\n\n#pickFromCloud{\n\tdisplay: inline-block;\n\tbackground-position: 16px center;\n\tpadding: 12px;\n\tpadding-left: 44px;\n}\n\n</style>\n"],"sourceRoot":""}]);
